	module.exports = {"data":{"allMarkdownRemark":{"edges":[{"node":{"html":"<p>class: middle center</p>\n<h1>HTML SETS IT UP</h1>\n<hr>\n<p>class: top center</p>\n<h1>APPS ARE EVERYWHERE</h1>\n<p><img src=\"https://sjunkins.files.wordpress.com/2014/06/iste-posters-001.jpg\" alt=\"inline\"></p>\n<hr>\n<h1>H T M L</h1>\n<p><img src=\"http://www.99lime.com/_bak/topics/html-is-easy/assets/example4.jpg\" alt=\"inline\"></p>\n<p>Hyper-Text Markup Language is the core of all websites\nProvides the web browsers a blueprint of all content with formatting</p>\n<hr>\n<p>class: center</p>\n<p><img src=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.36.10-PM.png\" alt=\"inline\"></p>\n<p>This is HTML</p>\n<hr>\n<p>class: middle center</p>\n<h1>HTML is made up of ELEMENTS &#x3C;/></h1>\n<hr>\n<p>class: center</p>\n<h2>Syntax</h2>\n<p><img src=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.38.52-PM.png\" alt=\"inline\"></p>\n<hr>\n<p>class: middle center</p>\n<h2>HTML Elements Can Store Data</h2>\n<h3>This type of data is called an “attribute”</h3>\n<hr>\n<p>class: center</p>\n<h2>Attributes</h2>\n<p><img src=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.41.08-PM.png\" alt=\"inline\"></p>\n<hr>\n<p>class: center</p>\n<h2>Attributes: Names &#x26; Values</h2>\n<p><img src=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.42.50-PM.png\" alt=\"inline\"></p>\n<hr>\n<p>class: center\n<img src=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.42.57-PM.png\" alt=\"inline\"></p>\n<hr>\n<p>class: center</p>\n<h2>Self Closing Tags</h2>\n<p><img src=\"http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.43.08-PM.png\" alt=\"inline\"></p>\n<hr>\n<h2>More Tags</h2>\n<p>All HTML Tags are listed here: <a href=\"http://www.htmldog.com/references/html/tags/\">http://www.htmldog.com/references/html/tags/</a>\n<a href=\"https://developer.mozilla.org/en-US/docs/Web/HTML/Element/head\">https://developer.mozilla.org/en-US/docs/Web/HTML/Element/head</a>\nDon’t try to memorize them! Simply refer back to documentation as needed.\nSome tags:</p>\n<p> Stands for paragraph; paragraphs go in here </p>\n<header> for headers </header>\n<nav> for navigation bars </nav>\n<footer> for footers </footer>\n---\n## Ordered & Unordered Lists\nOrdered & Unordered Lists\n<ul>\n\t<li>List Item</li>\n</ul>\n<ol>\n\t<li>List Item</li>\n</ol>\n---\n## Common Tags\n<p>Headings:</p>\n<h1> </h1> - Heading 1 (Largest Heading)\n<h2> </h2> - Heading 2 (Next Largest Heading)\n<h3> </h3> - Heading 3\n<p>Containers:</p>\n<html> </html> - Wraps the entire page\n<head> </head> - Wraps the header of the page\n<body> </body> - Wraps the main content\n<div> </div> - IMPORTANT. Stands for Division. It’s what is used 100% of websites\n<p> </p> - Wraps individual Paragraphs\nOthers:\n<strong> (bold), <em> (emphasis)\n<img> (images), <a href> (links), <li> (list items) , <title> (title),\n<br> (line break), <table> (tables), <!-- --> (comments)\n---\nclass: center\n# Exercise: Let’s Make A Site Together\n<p><a href=\"https://codepen.io/pen/?editors=1100\">https://codepen.io/pen/?editors=1100</a></p>\n<hr>\n<p>class: middle center</p>\n<h1>HTML Semantics</h1>\n<hr>\n<p>class: center</p>\n<div><img src=\"https://s3.amazonaws.com/viking_education/web_development/web_app_eng/html5_sectioning_specific_post.jpg\" width=\"80%\"></div>","internal":{"content":"---\ntitle: \"DAY 2 - HTML Sets It Up\"\ncourseId:  \"CFSG105\"\ncirriculum:\n  - \"Web / Full Stack / Software Development\"\ntype: \"slide\"\ndraft: false\n---\n\nclass: middle center\n\n# HTML SETS IT UP\n\n---\n\nclass: top center\n\n# APPS ARE EVERYWHERE\n\n![inline](https://sjunkins.files.wordpress.com/2014/06/iste-posters-001.jpg)\n\n---\n#  H T M L\n\n![inline](http://www.99lime.com/_bak/topics/html-is-easy/assets/example4.jpg)\n\nHyper-Text Markup Language is the core of all websites\nProvides the web browsers a blueprint of all content with formatting\n\n---\nclass: center\n\n![inline](http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.36.10-PM.png)\n\n\nThis is HTML\n\n---\nclass: middle center\n# HTML is made up of ELEMENTS </>\n---\nclass: center\n\n## Syntax\n\n![inline](http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.38.52-PM.png)\n\n---\n\nclass: middle center\n\n## HTML Elements Can Store Data\n### This type of data is called an “attribute”\n\n---\nclass: center\n## Attributes\n\n![inline](http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.41.08-PM.png)\n\n---\nclass: center\n## Attributes: Names & Values\n\n![inline](http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.42.50-PM.png)\n\n---\nclass: center\n![inline](http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.42.57-PM.png)\n\n---\nclass: center\n## Self Closing Tags\n\n![inline](http://www.valleytechnicalacademy.com/wp-content/uploads/2018/05/Screen-Shot-2018-05-02-at-4.43.08-PM.png)\n\n---\n## More Tags\n\nAll HTML Tags are listed here: http://www.htmldog.com/references/html/tags/\nhttps://developer.mozilla.org/en-US/docs/Web/HTML/Element/head\nDon’t try to memorize them! Simply refer back to documentation as needed.\nSome tags:\n\n<p> Stands for paragraph; paragraphs go in here </p>\n<header> for headers </header>\n<nav> for navigation bars </nav>\n<footer> for footers </footer>\n---\n## Ordered & Unordered Lists\nOrdered & Unordered Lists\n\n<ul>\n\t<li>List Item</li>\n</ul>\n<ol>\n\t<li>List Item</li>\n</ol>\n---\n## Common Tags\n\nHeadings:\n<h1> </h1> - Heading 1 (Largest Heading)\n<h2> </h2> - Heading 2 (Next Largest Heading)\n<h3> </h3> - Heading 3\n\nContainers:\n<html> </html> - Wraps the entire page\n<head> </head> - Wraps the header of the page\n<body> </body> - Wraps the main content\n<div> </div> - IMPORTANT. Stands for Division. It’s what is used 100% of websites\n<p> </p> - Wraps individual Paragraphs\nOthers:\n<strong> (bold), <em> (emphasis)\n<img> (images), <a href> (links), <li> (list items) , <title> (title),\n<br> (line break), <table> (tables), <!-- --> (comments)\n---\nclass: center\n# Exercise: Let’s Make A Site Together\n\nhttps://codepen.io/pen/?editors=1100\n\n---\nclass: middle center\n# HTML Semantics\n---\nclass: center\n<div><img src=\"https://s3.amazonaws.com/viking_education/web_development/web_app_eng/html5_sectioning_specific_post.jpg\" width=\"80%\"></div>\n"},"fields":{"slug":"/courses/coding-from-scratch/slides/day2-html/"}}}]}},"pathContext":{"slug":"/courses/coding-from-scratch/slides/day2-html/"}}

